import { createRouter, createWebHashHistory } from 'vue-router'
import index from '../views'
import home from '../views/pages/home.vue'
import login from '../views/pages/login.vue'
import newsList from '@/views/pages/news/list'
import aboutusList from '@/views/pages/aboutus/list'
import aboutusDetail from '@/views/pages/aboutus/formModel'
import aboutusAdd from '@/views/pages/aboutus/formAdd'
import systemintroList from '@/views/pages/systemintro/list'
import systemintroDetail from '@/views/pages/systemintro/formModel'
import systemintroAdd from '@/views/pages/systemintro/formAdd'
import zukeList from '@/views/pages/zuke/list'
import zukeDetail from '@/views/pages/zuke/formModel'
import zukeAdd from '@/views/pages/zuke/formAdd'
import zukeRegister from '@/views/pages/zuke/register'
import zukeCenter from '@/views/pages/zuke/center'
import fangdongList from '@/views/pages/fangdong/list'
import fangdongDetail from '@/views/pages/fangdong/formModel'
import fangdongAdd from '@/views/pages/fangdong/formAdd'
import fangyuanxinxiList from '@/views/pages/fangyuanxinxi/list'
import fangyuanxinxiDetail from '@/views/pages/fangyuanxinxi/formModel'
import fangyuanxinxiAdd from '@/views/pages/fangyuanxinxi/formAdd'
import storeupList from '@/views/pages/storeup/list'
import yuyuekanfangList from '@/views/pages/yuyuekanfang/list'
import yuyuekanfangDetail from '@/views/pages/yuyuekanfang/formModel'
import yuyuekanfangAdd from '@/views/pages/yuyuekanfang/formAdd'
import fangwuzulinList from '@/views/pages/fangwuzulin/list'
import fangwuzulinDetail from '@/views/pages/fangwuzulin/formModel'
import fangwuzulinAdd from '@/views/pages/fangwuzulin/formAdd'
import zulinhetongList from '@/views/pages/zulinhetong/list'
import zulinhetongDetail from '@/views/pages/zulinhetong/formModel'
import zulinhetongAdd from '@/views/pages/zulinhetong/formAdd'
import messagesList from '@/views/pages/messages/list'
import fangdongshensuList from '@/views/pages/fangdongshensu/list'
import fangdongshensuDetail from '@/views/pages/fangdongshensu/formModel'
import fangdongshensuAdd from '@/views/pages/fangdongshensu/formAdd'
import fangwuzixunList from '@/views/pages/fangwuzixun/list'
import fangwuzixunDetail from '@/views/pages/fangwuzixun/formModel'
import fangwuzixunAdd from '@/views/pages/fangwuzixun/formAdd'

const routes = [{
		path: '/',
		redirect: '/index/home'
	},
	{
		path: '/index',
		component: index,
		children: [{
			path: 'home',
			component: home
		}
		, {
			path: 'newsList',
			component: newsList
		}
		, {
			path: 'aboutusList',
			component: aboutusList
		}, {
			path: 'aboutusDetail',
			component: aboutusDetail
		}, {
			path: 'aboutusAdd',
			component: aboutusAdd
		}
		, {
			path: 'systemintroList',
			component: systemintroList
		}, {
			path: 'systemintroDetail',
			component: systemintroDetail
		}, {
			path: 'systemintroAdd',
			component: systemintroAdd
		}
		, {
			path: 'zukeList',
			component: zukeList
		}, {
			path: 'zukeDetail',
			component: zukeDetail
		}, {
			path: 'zukeAdd',
			component: zukeAdd
		}
		, {
			path: 'zukeCenter',
			component: zukeCenter
		}
		, {
			path: 'fangdongList',
			component: fangdongList
		}, {
			path: 'fangdongDetail',
			component: fangdongDetail
		}, {
			path: 'fangdongAdd',
			component: fangdongAdd
		}
		, {
			path: 'fangyuanxinxiList',
			component: fangyuanxinxiList
		}, {
			path: 'fangyuanxinxiDetail',
			component: fangyuanxinxiDetail
		}, {
			path: 'fangyuanxinxiAdd',
			component: fangyuanxinxiAdd
		}
        , {
            path: 'storeupList',
            component: storeupList
        }
		, {
			path: 'yuyuekanfangList',
			component: yuyuekanfangList
		}, {
			path: 'yuyuekanfangDetail',
			component: yuyuekanfangDetail
		}, {
			path: 'yuyuekanfangAdd',
			component: yuyuekanfangAdd
		}
		, {
			path: 'fangwuzulinList',
			component: fangwuzulinList
		}, {
			path: 'fangwuzulinDetail',
			component: fangwuzulinDetail
		}, {
			path: 'fangwuzulinAdd',
			component: fangwuzulinAdd
		}
		, {
			path: 'zulinhetongList',
			component: zulinhetongList
		}, {
			path: 'zulinhetongDetail',
			component: zulinhetongDetail
		}, {
			path: 'zulinhetongAdd',
			component: zulinhetongAdd
		}
		, {
			path: 'messagesList',
			component: messagesList
		}
		, {
			path: 'fangdongshensuList',
			component: fangdongshensuList
		}, {
			path: 'fangdongshensuDetail',
			component: fangdongshensuDetail
		}, {
			path: 'fangdongshensuAdd',
			component: fangdongshensuAdd
		}
		, {
			path: 'fangwuzixunList',
			component: fangwuzixunList
		}, {
			path: 'fangwuzixunDetail',
			component: fangwuzixunDetail
		}, {
			path: 'fangwuzixunAdd',
			component: fangwuzixunAdd
		}
		]
	},
	{
		path: '/login',
		component: login
	}
	,{
		path: '/zukeRegister',
		component: zukeRegister
	}
]

const router = createRouter({
  history: createWebHashHistory(process.env.BASE_URL),
  routes
})

export default router
