				// 
				.collect_view {
					border: 0px solid #eee;
					cursor: pointer;
					border-radius: 10px;
					padding: 10px 0px;
					color: #999;
					background: #fff;
					display: flex;
					width: auto;
					line-height: 1;
					justify-content: center;
					align-items: center;
					.iconfont {
						margin: 0 5px 0 0;
						color: #888;
					}
					.iconfontActive {
						margin: 0 5px 0 0;
						color: #feb043;
					}
					span {
						color: #666;
					}
					.textActive {
						color: #feb043;
					}
				}
				.collect_view:hover {
				}
				.collect_view:active {
					transform: scale(0.8);
				}
			}

			.info_item {

				.info_label {
				}
				.info_text {
				}
			}
			.btn_view {
				// -
				.cross_btn {
				}
				// 
				.cross_btn:hover {
				}
				// -
				.approval_btn {
				}
				// 
				.approval_btn:hover {
				}
				// -
				.edit_btn {
				}
				// 
				.edit_btn:hover {
				}
				// -
				.del_btn {
				}
				// 
				.del_btn:hover {
				}
			}
		}
	}

	//
	.tabs_view {
		:deep(.el-tabs__header) {
			background: transparent;
			border: none;
		}
		// 
		:deep(.el-tabs__nav-scroll) {
			.el-tabs__nav {
				.el-tabs__item {
				}
				.el-tabs__item:hover {
				}
				.is-active {
				}
			}
		}
		// 
		:deep(.el-tabs__content) {
		}
		//
		//
		.my_comment_view {

			.my_comment_form {
				// 
				:deep(.el-textarea__inner) {
				}
			}
			// 
			.comment_btn {
				// 
				.add_btn {
				}
				// 
				.add_btn:hover {
				}
				// 
				.reset_btn {
				}
				// 
				.reset_btn:hover {
				}
			}
		}

		//
		.comment_list {

			.comment {
				.comment_top {

					.comment_user {
						.comment_user_img {

							img {
								border-radius: 50%;
								width: 40px;
								height: 40px;
