<script setup>
	import {
		ref,
		getCurrentInstance
	} from 'vue';
	import moment from 'moment'
	import {
		useRouter
	} from 'vue-router';
	const context = getCurrentInstance()?.appContext.config.globalProperties;
	const router = useRouter()
	//
	const aboutUsDetail = ref({})
	const getAboutUs = () => {
		context?.$http({
			url: 'aboutus/detail/1',
			method: 'get',
		}).then(res=>{
			aboutUsDetail.value = res.data.data
		})
	}
	//
	const systemInfoDetail = ref({})
	const getSystemInfo = () => {
		context?.$http({
			url: 'systemintro/detail/1',
			method: 'get',
		}).then(res=>{
			systemInfoDetail.value = res.data.data
		})
	}
	//
	const fangyuanxinxiRecomList = ref([])
	const getfangyuanxinxiRecomList = () => {
		let autoSortUrl = 'fangyuanxinxi/autoSort'
		if(context?.$toolUtil.storageGet('frontToken')){
			autoSortUrl = "fangyuanxinxi/autoSort2"
		}
		let params = {
			sfsh: '',
			page: 1,
			limit: 4
		}
		context?.$http({
			url: autoSortUrl,
			method: 'get',
			params: params
		}).then(res => {
			fangyuanxinxiRecomList.value = res.data.data.list
			fangyuanxinxiRecomList.value.forEach(item=>{
				if(!isHttp(item.fangwutupian)){
					item.imgUrls = item.fangwutupian.split(',').map(item=>context.$config.url+item)
				}
			})
		})
	}
	//
	import formModel from './news/formModel'
	const newsFormModelRef = ref(null)
	//
	const newsList = ref([])
	const getNewsList = () => {
		context?.$http({
			url: 'news/list',
			method: 'get',
			params:{
				page:1,
				limit: 6,
                sort:'id',
                order:'desc',
			}
		}).then(res=>{
			newsList.value = res.data.data.list
			newsList.value.forEach(item=>{
				if(!isHttp(item.picture)){
					item.imgUrl=context.$config.url+item.picture.split(',')[0]
				}
			})
		})
	}
	const newsDetailClick = (item) => {
		if (item && item.id){
			newsFormModelRef.value.init(item.id)
		}
	}
	//http
	const isHttp = (str) => {
        return str && str.substr(0,4)=='http';
    }
	//
	const detailClick = (table,id) => {
		router.push(`/index/${table}Detail?id=${id}`)
	}
	const moreClick = (table) => {
		router.push(`/index/${table}List`)
	}
	const init = () => {
		getAboutUs()
		getSystemInfo()
		//
		getfangyuanxinxiRecomList()
		//
		getNewsList()
	}
	init()
</script>

