<template>
	<div class="edit_view" :style='{}'>
        <div class="breadcrumb-wrapper" style="width: 100%;">
            <div class="bread_view">
                <el-breadcrumb separator="" class="breadcrumb">
                    <el-breadcrumb-item class="first_breadcrumb" :to="{ path: '/' }"></el-breadcrumb-item>
                    <el-breadcrumb-item class="second_breadcrumb" v-for="(item,index) in breadList" :key="index">{{item.name}}</el-breadcrumb-item>
                </el-breadcrumb>
            </div>
        </div>
		<el-form ref="formRef" :model="form" class="add_form" label-width="120px" :rules="rules">
			<el-row>
				<el-col :span="12">
					<el-form-item label="" prop="fangwumingcheng">
						<el-input class="list_inp"
                                  v-model="form.fangwumingcheng"
                                  placeholder=""
                                  type="text"
							      :readonly="!isAdd||disabledForm.fangwumingcheng?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="24">
					<el-form-item label="" prop="fangwutupian">
						<uploads
							:disabled="!isAdd||disabledForm.fangwutupian?true:false"
							action="file/upload" 
							tip=""
							style="width: 100%;text-align: left;"
							:fileUrls="form.fangwutupian?form.fangwutupian:''" 
							@change="fangwutupianUploadSuccess">
						</uploads>
					</el-form-item>
				</el-col>
				<el-col :span="12">
					<el-form-item label="/m3" prop="fangwumianji">
						<el-input class="list_inp"
                                  v-model="form.fangwumianji"
                                  placeholder="/m3"
                                  type="text"
							      :readonly="!isAdd||disabledForm.fangwumianji?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="12">
					<el-form-item label="" prop="fangwudizhi">
						<el-input class="list_inp"
                                  v-model="form.fangwudizhi"
                                  placeholder=""
                                  type="text"
							      :readonly="!isAdd||disabledForm.fangwudizhi?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="12">
					<el-form-item label="" prop="zulinjiage">
						<el-input class="list_inp"
                                  v-model.number="form.zulinjiage"
                                  placeholder=""
                                  type="number"
							      :readonly="!isAdd||disabledForm.zulinjiage?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="24">
					<el-form-item label="" prop="huxing">
						<uploads
							:disabled="!isAdd||disabledForm.huxing?true:false"
							action="file/upload" 
							tip=""
							style="width: 100%;text-align: left;"
							:fileUrls="form.huxing?form.huxing:''" 
							@change="huxingUploadSuccess">
						</uploads>
					</el-form-item>
				</el-col>
				<el-col :span="12">
					<el-form-item label="" prop="fangdong">
						<el-input class="list_inp"
                                  v-model="form.fangdong"
                                  placeholder=""
                                  type="text"
							      :readonly="!isAdd||disabledForm.fangdong?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="12">
					<el-form-item label="" prop="fangdongxingming">
						<el-input class="list_inp"
                                  v-model="form.fangdongxingming"
                                  placeholder=""
                                  type="text"
							      :readonly="!isAdd||disabledForm.fangdongxingming?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="12">
					<el-form-item label="" prop="zukeming">
						<el-input class="list_inp"
                                  v-model="form.zukeming"
                                  placeholder=""
                                  type="text"
							      :readonly="!isAdd||disabledForm.zukeming?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="12">
					<el-form-item label="" prop="xingming">
						<el-input class="list_inp"
                                  v-model="form.xingming"
                                  placeholder=""
                                  type="text"
							      :readonly="!isAdd||disabledForm.xingming?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="12">
					<el-form-item label="/" prop="zulinshijian">
						<el-input class="list_inp"
                                  v-model.number="form.zulinshijian"
                                  placeholder="/"
                                  type="text"
							      :readonly="!isAdd||disabledForm.zulinshijian?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="12">
					<el-form-item label="" prop="zulinrenshu">
						<el-input class="list_inp"
                                  v-model.number="form.zulinrenshu"
                                  placeholder=""
                                  type="text"
							      :readonly="!isAdd||disabledForm.zulinrenshu?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="12">
					<el-form-item label="" prop="zulinjine">
						<el-input class="list_inp" v-model="zulinjine" placeholder="" readonly></el-input>
					</el-form-item>
				</el-col>
				<el-col :span="12">
					<el-form-item label="" prop="hetongriqi">
						<el-date-picker
							class="list_date"
							v-model="form.hetongriqi"
							format="YYYY-MM-DD HH:mm:ss"
							value-format="YYYY-MM-DD HH:mm:ss"
							type="datetime"
							style="width:100%;"
							:readonly="!isAdd||disabledForm.hetongriqi?true:false"
							placeholder="" />
					</el-form-item>
				</el-col>
			</el-row>
			<div class="formModel_btn_box">
				<el-button class="formModel_cancel" @click="backClick"></el-button>
				<el-button class="formModel_confirm"
                           @click="save"
                           type="success"
				>
					
				</el-button>
			</div>
		</el-form>
	</div>
</template>
<script setup>
	import {
		ref,
		getCurrentInstance,
		watch,
		onUnmounted,
		onMounted,
		nextTick,
		computed
	} from 'vue';
	import {
		useRoute,
		useRouter
	} from 'vue-router';
    import moment from "moment";
	const context = getCurrentInstance()?.appContext.config.globalProperties;
	const route = useRoute()
	const router = useRouter()
	//
	const tableName = 'zulinhetong'
	const formName = ''
	//
	const breadList = ref([{
		name: formName
	}])
	//
	const getUUID =()=> {
      return new Date().getTime();
    }
	//form
	const form = ref({
		fangwumingcheng: '',
		fangwutupian: '',
		fangwumianji: '',
		fangwudizhi: '',
		zulinjiage: 0,
		huxing: '',
		fangdong: '',
		fangdongxingming: '',
		zukeming: '',
		xingming: '',
		zulinshijian: 0,
		zulinrenshu: 0,
		zulinjine: 0,
		hetongriqi: '',
	})
	const formRef = ref(null)
	const id = ref(0)
	const type = ref('')
	const disabledForm = ref({
		fangwumingcheng : false,
		fangwutupian : false,
		fangwumianji : false,
		fangwudizhi : false,
		zulinjiage : false,
		huxing : false,
		fangdong : false,
		fangdongxingming : false,
		zukeming : false,
		xingming : false,
		zulinshijian : false,
		zulinrenshu : false,
		zulinjine : false,
		hetongriqi : false,
	})
	const isAdd = ref(false)
	//
	const rules = ref({
		fangwumingcheng: [
			{required: true,message: '',trigger: 'blur'}, 
		],
		fangwutupian: [
		],
		fangwumianji: [
		],
		fangwudizhi: [
		],
		zulinjiage: [
			{required: true,message: '',trigger: 'blur'}, 
			{ validator: context.$toolUtil.validator.number, trigger: 'blur' },
		],
		huxing: [
		],
		fangdong: [
		],
		fangdongxingming: [
		],
		zukeming: [
		],
		xingming: [
		],
		zulinshijian: [
			{ validator: context.$toolUtil.validator.intNumber, trigger: 'blur' },
		],
		zulinrenshu: [
			{ validator: context.$toolUtil.validator.intNumber, trigger: 'blur' },
		],
		zulinjine: [
			{ validator: context.$toolUtil.validator.number, trigger: 'blur' },
		],
		hetongriqi: [
		],
	})
	//
	const fangwutupianUploadSuccess=(e)=>{
		form.value.fangwutupian = e
	}
	//
	const huxingUploadSuccess=(e)=>{
		form.value.huxing = e
	}
	const zulinjine =computed(()=>{
		let c = form.value
        let a = Number(c.zulinjiage)*Number(c.zulinshijian)
        form.value.zulinjine = Number(a)?Number(parseFloat(a).toFixed(2)) : 0
        return Number(a)?Number(parseFloat(a).toFixed(2)) : 0
	})
	//methods

	//methods
	//info
	const getInfo = ()=>{
		context?.$http({
			url: `${tableName}/info/${id.value}`,
			method: 'get'
		}).then(res => {
			let reg=new RegExp('../../../file','g')
			form.value = res.data.data
		})
	}
	const crossRow = ref('')
	const crossTable = ref('')
	const crossTips = ref('')
	const crossColumnName = ref('')
	const crossColumnValue = ref('')
	//
	const init = (formId=null,formType='add',formNames='',row=null,table=null,statusColumnName=null,tips=null,statusColumnValue=null) => {
			form.value.hetongriqi = context?.$toolUtil.getCurDateTime()
		if(formId){
			id.value = formId
			type.value = formType
		}
		if(formType == 'add'){
			isAdd.value = true
		}else if(formType == 'info'){
			isAdd.value = false
			getInfo()
		}else if(formType == 'edit'){
			isAdd.value = true
			getInfo()
		}
		else if(formType == 'cross'){
			isAdd.value = true
			// getInfo()
			for(let x in row){
				if(x=='fangwumingcheng'){
					form.value.fangwumingcheng = row[x];
					disabledForm.value.fangwumingcheng = true;
					continue;
				}
				if(x=='fangwutupian'){
					form.value.fangwutupian = row[x];
					disabledForm.value.fangwutupian = true;
					continue;
				}
				if(x=='fangwumianji'){
					form.value.fangwumianji = row[x];
					disabledForm.value.fangwumianji = true;
					continue;
				}
				if(x=='fangwudizhi'){
					form.value.fangwudizhi = row[x];
					disabledForm.value.fangwudizhi = true;
					continue;
				}
				if(x=='zulinjiage'){
					form.value.zulinjiage = row[x];
					disabledForm.value.zulinjiage = true;
					continue;
				}
				if(x=='huxing'){
					form.value.huxing = row[x];
					disabledForm.value.huxing = true;
					continue;
				}
				if(x=='fangdong'){
					form.value.fangdong = row[x];
					disabledForm.value.fangdong = true;
					continue;
				}
				if(x=='fangdongxingming'){
					form.value.fangdongxingming = row[x];
					disabledForm.value.fangdongxingming = true;
					continue;
				}
				if(x=='zukeming'){
					form.value.zukeming = row[x];
					disabledForm.value.zukeming = true;
					continue;
				}
				if(x=='xingming'){
					form.value.xingming = row[x];
					disabledForm.value.xingming = true;
					continue;
				}
				if(x=='zulinshijian'){
					form.value.zulinshijian = row[x];
					disabledForm.value.zulinshijian = true;
					continue;
				}
				if(x=='zulinrenshu'){
					form.value.zulinrenshu = row[x];
					disabledForm.value.zulinrenshu = true;
					continue;
				}
				if(x=='zulinjine'){
					form.value.zulinjine = row[x];
					disabledForm.value.zulinjine = true;
					continue;
				}
				if(x=='hetongriqi'){
					form.value.hetongriqi = row[x];
					disabledForm.value.hetongriqi = true;
					continue;
				}
			}
			if(row){
				crossRow.value = row
			}
			if(table){
				crossTable.value = table
			}
			if(tips){
				crossTips.value = tips
			}
			if(statusColumnName){
				crossColumnName.value = statusColumnName
			}
			if(statusColumnValue){
				crossColumnValue.value = statusColumnValue
			}
		}
		context?.$http({
			url: `${context?.$toolUtil.storageGet('frontSessionTable')}/session`,
			method: 'get'
		}).then(res => {
			var json = res.data.data
			if(json.hasOwnProperty('fangdong') && context?.$toolUtil.storageGet("frontRole")!=""){
				form.value.fangdong = json.fangdong
				disabledForm.value.fangdong = true;
			}
			if(json.hasOwnProperty('fangdongxingming') && context?.$toolUtil.storageGet("frontRole")!=""){
				form.value.fangdongxingming = json.fangdongxingming
				disabledForm.value.fangdongxingming = true;
			}
			if(json.hasOwnProperty('zukeming') && context?.$toolUtil.storageGet("frontRole")!=""){
				form.value.zukeming = json.zukeming
				disabledForm.value.zukeming = true;
			}
			if(json.hasOwnProperty('xingming') && context?.$toolUtil.storageGet("frontRole")!=""){
				form.value.xingming = json.xingming
				disabledForm.value.xingming = true;
			}
            if (localStorage.getItem('autoSave')) {
                localStorage.removeItem('autoSave')
                save()
            }
		})
	}
	//
	//
	const backClick = () => {
		history.back()
	}
	//
	const save=()=>{
		if(form.value.fangwutupian!=null) {
			form.value.fangwutupian = form.value.fangwutupian.replace(new RegExp(context?.$config.url,"g"),"");
		}
		if(form.value.huxing!=null) {
			form.value.huxing = form.value.huxing.replace(new RegExp(context?.$config.url,"g"),"");
		}
		var table = crossTable.value
		var objcross = JSON.parse(JSON.stringify(crossRow.value))
		let crossUserId = ''
		let crossRefId = ''
		let crossOptNum = ''
		if(type.value == 'cross'){
			if(crossColumnName.value!=''){
				if(!crossColumnName.value.startsWith('[')){
					for(let o in objcross){
						if(o == crossColumnName.value){
							objcross[o] = crossColumnValue.value
						}
					}
					//
					changeCrossData(objcross)
				}else{
					crossUserId = context?.$toolUtil.storageGet('userid')
					crossRefId = objcross['id']
					crossOptNum = crossColumnName.value.replace(/\[/,"").replace(/\]/,"")
				}
			}
		}
		formRef.value.validate((valid)=>{
			if(valid){
				if(crossUserId&&crossRefId){
					form.value.crossuserid = crossUserId
					form.value.crossrefid = crossRefId
					let params = {
						page: 1,
						limit: 1000, 
						crossuserid:form.value.crossuserid,
						crossrefid:form.value.crossrefid,
					}
					context?.$http({
						url: `${tableName}/page`,
						method: 'get', 
						params: params 
					}).then(res=>{
						if(res.data.data.total>=crossOptNum){
							context?.$toolUtil.message(`${crossTips.value}`,'error')
							return false
						}else{
							context?.$http({
								url: `${tableName}/${!form.value.id ? "save" : "update"}`,
								method: 'post', 
								data: form.value 
							}).then(res=>{
                                context?.$toolUtil.message(``,'success')
                                history.back()
							})
						}
					})
				}else{
					context?.$http({
						url: `${tableName}/${!form.value.id ? "save" : "update"}`,
						method: 'post', 
						data: form.value 
					}).then(res=>{
                        context?.$toolUtil.message(``,'success')
                        history.back()
					})
				}
			}
		})
	}
	//
	const changeCrossData=(row)=>{
        if(type.value == 'cross'){
            context?.$http({
                url: `${crossTable.value}/update`,
                method: 'post',
                data: row
            }).then(res=>{})
        }
	}
	onMounted(()=>{
		type.value = route.query.type?route.query.type:'add'
		let row = null
		let table = null
		let statusColumnName = null
		let tips = null
		let statusColumnValue = null
		if(type.value == 'cross'){
			row = context?.$toolUtil.storageGet('crossObj')?JSON.parse(context?.$toolUtil.storageGet('crossObj')):{}
			table = context?.$toolUtil.storageGet('crossTable')
			statusColumnName = context?.$toolUtil.storageGet('crossStatusColumnName')
			tips = context?.$toolUtil.storageGet('crossTips')
			statusColumnValue = context?.$toolUtil.storageGet('crossStatusColumnValue')
		}
		init(route.query.id?route.query.id:null, type.value,'', row, table, statusColumnName, tips, statusColumnValue)
	})
    onUnmounted(()=>{
        Object.keys(localStorage).map(item=>{
            if(item.startsWith('cross')){
                localStorage.removeItem(item)
            }
        })
    })
</script>
<style lang="scss" scoped>
	// 
	.bread_view {
		:deep(.breadcrumb) {
			.el-breadcrumb__separator {
			}
			.first_breadcrumb {
				.el-breadcrumb__inner {
				}
			}
			.second_breadcrumb {
				.el-breadcrumb__inner {
				}
			}
		}
	}
	// 
	.add_form{
		// form item
		:deep(.el-form-item) {
			//label
			.el-form-item__label {
			}
			// 
			.el-form-item__content {
				// 
				.list_inp {

				}
				//
				.list_date {
				}
				//
				.el-upload-list  {
					//
					.el-upload__tip {
					}
					//
					.el-upload--picture-card {
						//
						.el-icon{
						}
					}
					.el-upload-list__item {
					}
				}
			}
		}
	}
	// 
	.formModel_btn_box {
		.formModel_cancel {
		}
		.formModel_cancel:hover {
		}
		
		.formModel_confirm {
		}
		.formModel_confirm:hover {
		}
	}
</style>
<style lang="scss">
